const osName = platform();

function showHide(element) {
	if (document.getElementById(element)) { 
    	var obj = document.getElementById(element); 
    	if (obj.style.display != "block") { 
        	obj.style.display = "block"; //Показываем элемент
    	}	
    	else obj.style.display = "none"; //Скрываем элемент
    }
}   



function TextDataRecord(props){
	return (	
		<Div className = "DataRecord">
			<Div className = "TextDataRecord">Выберите время для записи</Div>
			{props.firstData} - {props.secondData}
		</Div>
	)
}


function ButtonRecord(props){
	var idButton = props.day + props.time;
	return(
		<Button id = {idButton} className = "ButtonScheduleMenuTime" onClick = {() => props.confirm(props.time)}>
			<Div className = "TextButtonScheduleMenuTime">{props.time}</Div>
		</Button>
	)
}

function BlockButtonRecord(props){
	var times = props.times;
	return(
		<Div className = "ButtonBlocksScheduleMenuTime">
			<ButtonRecord confirm = {props.confirm} time = {times[0]} day = {props.day}/>
			<ButtonRecord confirm = {props.confirm} time = {times[1]} day = {props.day}/>
			<ButtonRecord confirm = {props.confirm} time = {times[2]} day = {props.day}/>
		</Div>
	)
}

function AllButtonsRecord(props){
	var firstTimes = ["11:00", "12:00", "13:00"];
	var secondTimes = ["14:00", "15:00", "16:00"];
	var thirdTimes = ["17:00", "18:00", "19:00"];
	var fourthTimes = ["20:00", "21:00", "22:00"];
	return(
		<Div>
			<BlockButtonRecord confirm = {props.onClick} times = {firstTimes} day = {props.day}/>
			<BlockButtonRecord confirm = {props.onClick} times = {secondTimes} day = {props.day}/>
			<BlockButtonRecord confirm = {props.onClick} times = {thirdTimes} day = {props.day}/>
			<BlockButtonRecord confirm = {props.onClick} times = {fourthTimes} day = {props.day}/>
		</Div>
	)
}

function ButtonDayRecord(props){
	return(
		<Button id = {props.day} className = "ButtonScheduleMenuDay" size="l" mode="outline" onClick = {props.confirm}>
			<Div className = "TextButtonScheduleMenuDay">{props.day}</Div>
		</Button>
	)
}
function GetNullRecordTimes(){
	var nullRecordTimes = new Map([
		["ПН", []],
		["ВТ", []],
		["СР", []],
		["ЧТ", []],
		["ПТ", []],
		["СБ", []],
		["ВС", []]
	]);
	return nullRecordTimes
}

function GetNullCountTimes(){
	var nullCountTimes = new Map([
		["ПН", 0],
		["ВТ", 0],
		["СР", 0],
		["ЧТ", 0],
		["ПТ", 0],
		["СБ", 0],
		["ВС", 0]
	]);
	return nullCountTimes;
}

function AlertConfirm(props){
	return(
		<Alert actionsLayout = "vertical" onClose = {props.close} >
			<Div className ="TextAlertConfirm">Вы записаны!</Div>
		</Alert>
	)
}

class ButtonsWeekRecord extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
				isConfirm: false,
				data : this.props.data,
				currentCountTimes: new Map([
					["ПН", 0],
					["ВТ", 0],
					["СР", 0],
					["ЧТ", 0],
					["ПТ", 0],
					["СБ", 0],
					["ВС", 0]
				]),
				prevDay : "ПН",
				activeDay : "ПН",
				recordTimes :new Map([
					["ПН", []],
					["ВТ", []],
					["СР", []],
					["ЧТ", []],
					["ПТ", []],
					["СБ", []],
					["ВС", []]
				])
		  };	
		  this.handleClickOnButtonDay = this.handleClickOnButtonDay.bind(this);	 
		  this.handleClickOnButtonRecord = this.handleClickOnButtonRecord.bind(this);
		  this.handleClickOnConfirm = this.handleClickOnConfirm.bind(this);
		  this.handleClickOnClose = this.handleClickOnClose.bind(this);
	  }
	  componentDidMount(){
		var element = document.getElementById(this.state.activeDay);
		element.style.opacity = "0.4";
	  }
	  componentDidUpdate(){
		console.log("ДАаааа");
		console.log( this.state.currentCountTimes.get(this.state.activeDay));
		console.log(this.state.recordTimes);
		var prevElement = document.getElementById(this.state.prevDay);
		prevElement.style.opacity = "1.0";
		var element = document.getElementById(this.state.activeDay);
		element.style.opacity = "0.4";
		NoPressButton(this.state.activeDay, this.state.data);
		PressButton(this.state.activeDay, this.state.recordTimes, this.state.currentCountTimes.get(this.state.activeDay));
	}

	async handleClickOnButtonDay(day){
		var newData =  await this.props.getRecord();
		console.log(newData);
		this.setState({
			prevDay: this.state.activeDay,
			activeDay : day,
			data : newData
		});
	}
	handleClickOnButtonRecord(time){
		if(isNoPressButton(this.state.activeDay, time, this.state.data)){
			return;
		}
		var day = this.state.activeDay;
		var newTimes = this.state.recordTimes;
		var timesDay = newTimes.get(day);
		if (timesDay.includes(time, 0)){
			var index = timesDay.indexOf(time, 0);
			newTimes.get(day).splice(index, 1);
			var newCountTimes = this.state.currentCountTimes;
			newCountTimes.set(this.stateactiveDay, this.state.currentCountTimes.get(this.state.activeDay) - 1);
			this.setState({
				currentCountTimes : newCountTimes,
				recordTimes : newTimes
			});
		}
		if (this.state.currentCountTimes.get(this.state.activeDay) == 4){
			return;
		}
		else{
			newTimes.get(day).push(time);
			var newCountTimes = this.state.currentCountTimes;
			newCountTimes.set(this.state.activeDay, this.state.currentCountTimes.get(this.state.activeDay) + 1);
			this.setState({
				currentCountTimes : newCountTimes,
				recordTimes : newTimes
			});
		}
	}
	async handleClickOnConfirm(){
		var result = new Map();
		for(var key of this.state.recordTimes.keys()){
			if(this.state.recordTimes.get(key).length == 0){
				continue;
			}			
			result.set(key, this.state.recordTimes.get(key));
		}
		console.log(result);
		await RecordOnWashing(result, this.state.data, this.props.user);
		var newData = await this.props.getRecord();
		var  nullRecordTimes = GetNullRecordTimes();
		var nullCountTimes = GetNullCountTimes();
		this.setState({
			data: newData,
			recordTimes : nullRecordTimes,
			currentCountTimes : nullCountTimes,
			isConfirm: true
		});
	}
	handleClickOnClose(){
		this.setState({
			isConfirm : false
		});
	}
	ChangeButton(day){
		var mapButtons = new Map([
			["ПН", <AllButtonsRecord day = "ПН" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>],
			["ВТ", <AllButtonsRecord day = "ВТ" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>],
			["СР", <AllButtonsRecord day = "СР" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>],
			["ЧТ", <AllButtonsRecord day = "ЧТ" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>],
			["ПТ", <AllButtonsRecord day = "ПТ" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>],
			["СБ", <AllButtonsRecord day = "СБ" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>],
			["ВС", <AllButtonsRecord day = "ВС" onClick = {this.handleClickOnButtonRecord}  recordTimes = {this.state.recordTimes}/>]
		]);
		return mapButtons.get(day);
	}
	  render(){
		var confirm = this.state.isConfirm ? <AlertConfirm close = {this.handleClickOnClose}/>: null;
		var change = this.state.activeDay == this.state.prevDay? 
			this.ChangeButton(this.state.prevDay) :this.ChangeButton(this.state.activeDay);
		return(
			<Div>
				{confirm}
				<Div className = "ButtonBlocksScheduleMenuDay">
					<ButtonDayRecord day = "ПН" confirm = {() => this.handleClickOnButtonDay("ПН")}/>
					<ButtonDayRecord day = "ВТ" confirm = {() => this.handleClickOnButtonDay("ВТ")}/>
					<ButtonDayRecord day = "СР" confirm = {() => this.handleClickOnButtonDay("СР")}/>
					<ButtonDayRecord day = "ЧТ" confirm = {() => this.handleClickOnButtonDay("ЧТ")}/>
					<ButtonDayRecord day = "ПТ" confirm = {() => this.handleClickOnButtonDay("ПТ")}/>
					<ButtonDayRecord day = "СБ" confirm = {() => this.handleClickOnButtonDay("СБ")}/>
					<ButtonDayRecord day = "ВС" confirm = {() => this.handleClickOnButtonDay("ВС")}/>
				</Div>
				<Div className = "TextScheduleMenuFreeTime">
					Свободное время :
				</Div>
				{change}
				<ButtonConfirm confirm = {() => this.handleClickOnConfirm()} />
			</Div>
		)
	  }
}

function PressButton(day, recordTimes){
	var arr = [
		"11:00", "12:00", "13:00",
		"14:00", "15:00", "16:00",
		"17:00", "18:00", "19:00",
		"20:00", "21:00", "22:00"
	];
	var items = recordTimes.get(day);
	console.log(items);
	for(var i= 0; i < arr.length; i++){
		var id = day+arr[i];
		var element = document.getElementById(id);
		if (items.includes(arr[i], 0)){
			console.log("ДА");
			element.style.opacity = "0.4";
		}
		else{
			element.style.opacity = "1.0";
		}
	}
}

function NoPressButton(day, data){
	var arr = [
		"11:00", "12:00", "13:00",
		"14:00", "15:00", "16:00",
		"17:00", "18:00", "19:00",
		"20:00", "21:00", "22:00"
	];
	console.log(WarningTimes);
	for(var i= 0; i < arr.length; i++){
		var id = day+arr[i];
		var element = document.getElementById(id);
		if (!data.has(day)){
			element.style.backgroundColor = "#00A897";
			continue;
		}
		var WarningTimes = data.get(day);
		if(!WarningTimes.has(arr[i])){
			element.style.backgroundColor = "#00A897";
			continue;
		}
		console.log(WarningTimes.get(arr[i]));
		if (Number(WarningTimes.get(arr[i])) > 6){
			console.log("ДА");
			element.style.backgroundColor = "#ff0000";
		}
		else{
			element.style.backgroundColor = "#00A897";
		}
	}
}

function isNoPressButton(day, time, data){
	if (!data.has(day)){
		return false;
	}
	var times = data.get(day);
	if (!times.has(time)){
		return false;
	}
	var count = times.get(time);
	console.log(count);
	return count > 6;
}

