class SheduleArea extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			activeDay : "ПН",
			recordTimes :[],
			currentCountDay : 0
		  };
		  this.handleClickOnButtonDay = this.handleClickOnButtonDay.bind(this);
		  this.handleClickOnButtonRecord = this.handleClickOnButtonRecord.bind(this);
	  }
	  handleClickOnButtonDay(day){
			this.setState({
				activeDay: day
			});
	  }
	  handleClickOnButtonRecord(time, stateButton){
			var newTimes = this.state.recordTimes.push(time)
			this.setState({
				recordTimes: newTimes
			})
	  }

	  render(){
		  return(
			<Div className="MainScheduleMenu">
				<TextDataRecord firstData = "22.06.2020" secondData = "28.06.2020"/>
				<ButtonsWeekRecord data = {this.props.data}  getRecord = {this.props.getRecord} user = {this.props.user}/>
			</Div>				
		  )
	  }
}

async function RecordOnWashing(result, data, user){
	if(result.size == 0){
		return;
	}
	var userId = user.id;
	for(var key of result.keys()){
		for(var time of result.get(key)){
			var conditionDay = data.has(key);
			if(conditionDay){
				var conditionTime = data.get(key).has(time);
				console.log(data.get(key));
				if(conditionTime){
					await UpdateRecord(key, time, data.get(key).get(time) + 1, userId);
					console.log("UPDATE");
					continue;
				}
			}
			await AddRecord(key, time, userId);
			console.log("ADD");
		}
	}
}

async function AddRecord(newDay, newTime, userId){
	var urlRecord = "https://cfe6e15b1fd0.ngrok.io/databaseRecordLearn";
	let response = await fetch(urlRecord, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({
			day: newDay,
			time: newTime,
			countUser: 1,
			user : userId
		}),  // типа запрашиаемого документа
    	headers: new Headers({
      		'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
		let answer = await response.text();
		console.log(response.text);
		return answer;
	} 
	return "NOCONNECT"; 
}

async function UpdateRecord(updateDay, updateTime, count){
	var urlRecord = "https://cfe6e15b1fd0.ngrok.io/databaseRecordLearn";
	let response = await fetch(urlRecord, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'PUT',              // метод POST
    	body: JSON.stringify({
			day: updateDay,
			time: updateTime,
			countUser: count
		}),  // типа запрашиаемого документа
    	headers: new Headers({
      	'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
		let answer = await response.text();
		console.log(response.text);
		return answer;
	} 
	return "NOCONNECT"; 
}



const ScheduleMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader className = "PanelHeaderScheduleMenu" left =
			{<PanelHeaderButton onClick={props.go} data-to="mainMenu"> 
			 {osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			 </PanelHeaderButton>}>
			<Div className = "PanelHeaderScheduleMenu">
				Регистрация
			</Div>
		</PanelHeader>
		<SheduleArea data = {props.data} getRecord = {props.getRecord} user = {props.fetchedUser}/>
	</Panel>
)


ScheduleMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default ScheduleMenu;