import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert';
import './StyleMyRecordMenu.css';

const osName = platform();

function showHide(element) {
	if (document.getElementById(element)) { 
    	var obj = document.getElementById(element); 
    	if (obj.style.display != "block") { 
        	obj.style.display = "block"; //Показываем элемент
    	}	
    	else obj.style.display = "none"; //Скрываем элемент
    }
}   

function ButtonMyRecord(props){
	return(
		<Button className = "ButtonMyRecordMenuDataTime" size="xl" mode="outline" onClick = {props.onClick}>
			<Div className = "TextButtonMyRecordMenuDataTime">{props.dataTime}</Div>
		</Button>
	)
}

function ButtonRecord(props){
	return(
			<Button className = "ButtonMyRecordMenuRecord" size="l" mode="outline" onClick = {() => props.delete(props.dataTime, props.time)}>
				<Div className = "TextButtonMyRecordMenuDataTime">{props.time}</Div>
			</Button>
	)	
}

function MainButtonRecord(props){
	var buttons = [];
	for (var i = 0; i< props.time.length; i++){
		buttons.push(<ButtonRecord close = {props.close} dataTime = {props.dataTime} time = {props.time[i]} delete = {props.delete}/>)
	}
	return(
		<Div>
			<ButtonMyRecord isConfirm = {props.isConfirm} dataTime = {props.dataTime} onClick = {() => props.onClick(props.id)}/>
			<Div className = "HiddenBlock" id = {props.id}>
				{buttons}
			</Div>
		</Div>
	)
}

function AlertConfirm(props){
	return(
		<Alert actionsLayout = "vertical" onClose = {props.close} >
			<Div className ="TextAlertConfirm">Удалить запись?</Div>
			<Div className = "BlockButton">
				<Button  className = "DeleteButton"  size="l" mode="outline" onClick = {props.delete}>ДА</Button>
				<Button  className = "DeleteButton"  size="l" mode="outline" onClick = {props.close}>НЕТ</Button>
			</Div>
		</Alert>
	)
}

var userDays = [
	"ПН", "ВТ", "СР", "ЧТ", "ПТ", "СБ", "ВС"
]

async function Delete(delDay, delTime, userId){
	var urlRecord = "https://cfe6e15b1fd0.ngrok.io/databaseRecordLearn";
	let response = await fetch(urlRecord, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'DELETE',              // метод POST
    	body: JSON.stringify({
			day: delDay,
			time: delTime,
			user : userId
		}),  // типа запрашиаемого документа
    	headers: new Headers({
      		'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
		let answer = await response.text();
		console.log(response.text);
		return answer;
	} 
	return "NOCONNECT"; 
}


class BlockButtonRecord extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
				isConfirm: false,
				delDataTime: null,
				delTime: null
		  };
		  this.handleClickOnButtonRecord = this.handleClickOnButtonRecord.bind(this);
		  this.handleClickOnButtonDelete = this.handleClickOnButtonDelete.bind(this);
		  this.handleClickOnButtonAlertConfirm = this.handleClickOnButtonAlertConfirm.bind(this);
		  this.handleClickOnButtonDeleteConfirm = this.handleClickOnButtonDeleteConfirm.bind(this);
	  }
	  handleClickOnButtonRecord(id){
			if (document.getElementById(id)) { 
				var obj = document.getElementById(id); 
				if (obj.style.display != "block") { 
					obj.style.display = "block"; //Показываем элемент
				}	
			else obj.style.display = "none"; //Скрываем элемент
			}
	  }
	  handleClickOnButtonDelete(day, time){
			this.setState({
				delDataTime: day,
				delTime: time,
				isConfirm: true
			});
	  }
	  async handleClickOnButtonDeleteConfirm(){
		var answer = await Delete(this.state.delDataTime, this.state.delTime, this.props.user.id);
		console.log(answer);
		await this.props.refresh();
		this.setState({
			delDataTime: null,
			delTime: null,
			isConfirm: false
		});	
	  }
	  handleClickOnButtonAlertConfirm(){
		  this.setState({
			  isConfirm: !this.state.isConfirm
		  })
	  }
	  render(){
		var alert = this.state.isConfirm ? <AlertConfirm close = {this.handleClickOnButtonAlertConfirm} delete = {this.handleClickOnButtonDeleteConfirm}/> : null;
		var activeRecord = [];
		var length = 0;
		var dataTimes = new Map();
		for (var key of this.props.dataUser.keys())
		{
			var users = this.props.dataUser.get(key);
			for(var user of users.values()){
				console.log(user);
				if(Number(user) == Number(this.props.user.id)){
					dataTimes.set(key, []);
					length++;
					break;
				}
			}
		}
		console.log(dataTimes);
		for (var key of dataTimes.keys()){
			var users = this.props.dataUser.get(key);
			for(var time of users){
				if(Number(time[1]) == Number(this.props.user.id)){
					dataTimes.get(key).push(time[0]);
				}
			}
		}
		console.log(dataTimes);
		console.log(length);
		for(var i= 0; i < userDays.length; i++){
			if (!dataTimes.has(userDays[i])){
				continue;
			}
			activeRecord.push(<MainButtonRecord  time = {dataTimes.get(userDays[i])}  dataTime = {userDays[i]} id = {"but"+i} onClick ={this.handleClickOnButtonRecord} delete = {this.handleClickOnButtonDelete}/>);
		}
			return(
			<Div className = "ButtonBlocksMyRecordMenuDataTime">
				{alert}
				{activeRecord}
			</Div>
			);
	  }

}


const MyRecordMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader left =
			{<PanelHeaderButton onClick={props.go} data-to="mainMenu"> 
			 {osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			 </PanelHeaderButton>}>
			<Div className = "PanelHeaderMyRecordMenu">
				Запись
			</Div>
		</PanelHeader>
		<Div className="MainMyRecordMenu">
			<BlockButtonRecord refresh={props.refresh} data = {props.data} dataUser = {props.dataUser} user = {props.fetchedUser}/>
		</Div>
	</Panel>
);
