import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import RegisterMenu from './panels/RegisterMenu';
import MainMenu from './panels/MainMenu';
import ScheduleMenu from './panels/ScheduleMenu';
import MyRecordMenu from './panels/MyRecordMenu';
import MyQRCodeMenu from './panels/MyQRCodeMenu';



async function GetRecordLearn(){
	var dict = new Map();
	var urlRecord = "https://cfe6e15b1fd0.ngrok.io/databaseRecordLearn";
	let response = await fetch(urlRecord);
	let records = await response.json(); // читаем ответ в формате JSON
	for(var i=0; i < records.length; i++){
		if(!dict.has(records[i].day)){
			var secondDict = new Map();
			secondDict.set(records[i].time, records[i].countUser);
			dict.set(records[i].day, secondDict);
		}
		else{
			dict.get(records[i].day).set(records[i].time, records[i].countUser)
		}
	}
	console.log("Я работаю");
	return dict;
}

async function GetUserRecordLearn(){
	var dict = new Map();
	var urlRecord = "https://cfe6e15b1fd0.ngrok.io/databaseRecordLearn";
	let response = await fetch(urlRecord)
	let records = await response.json(); // читаем ответ в формате JSON
	for(var i=0; i < records.length; i++){
		if(!dict.has(records[i].day)){
			var secondDict = new Map();
			secondDict.set(records[i].time, records[i].user);
			dict.set(records[i].day, secondDict);
		}
		else{
			dict.get(records[i].day).set(records[i].time, records[i].user)
		}
	}
	console.log("Я работаю");
	return dict;
}


const App = (props) => {
	const [activePanel, setActivePanel] = useState(props.active);
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [dataRecord, setDataRecord] = useState(null);
	const [dataUser, setDataUser] = useState(null);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();

	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};
	
	const goMain = () => {
		setActivePanel("mainMenu");
	};

	async function goRecord(){
		let data = await GetRecordLearn();
		console.log(data);
		setDataRecord(data);
		setActivePanel('scheduleMenu');
	}

	async function goMyRecord(){
		let data = await GetRecordLearn();
		let dataUser = await GetUserRecordLearn();
		console.log(data);
		setDataRecord(data);
		setDataUser(dataUser)
		setActivePanel('myRecordMenu');
	}

	async function Refresh(){
		let data = await GetRecordLearn();
		let dataUser = await GetUserRecordLearn();
		console.log(data);
		setDataRecord(data);
		setDataUser(dataUser)
	}

	return (
		<View activePanel={activePanel} popout={popout}>
			<RegisterMenu id='registerMenu' go={goMain}/>
			<MainMenu id='mainMenu' go={go}  goRecord = {goRecord} goMyRecord = {goMyRecord}/>
			<ScheduleMenu id='scheduleMenu' go={go} data = {dataRecord} getRecord = {GetRecordLearn} fetchedUser = {fetchedUser}/>
			<MyRecordMenu id='myRecordMenu' refresh={Refresh} go={go}  dataUser = {dataUser} data = {dataRecord} fetchedUser = {fetchedUser}/>
			<MyQRCodeMenu id='myQRCodeMenu' go={go}/>
		</View>
	);
}

export default App;

