const osName = platform();

async function IsRegistration(){
	var currentUser = await bridge.send('VKWebAppGetUserInfo');
	var keyDefault = document.getElementById("input-id").value;
	var urlUsers = "https://cfe6e15b1fd0.ngrok.io/databaseUsersLearnReg";
	var data = {idVk : currentUser.id, NumberRegistration : keyDefault};
	let response = await fetch(urlUsers, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify(data),  // типа запрашиаемого документа
    	headers: new Headers({
      		'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) {
		let answer = await response.text();
		console.log(answer);
  		if (answer == "1"){
			console.log(answer);
  			return true;
  		}
  		else{
			return false;
  		}
	} 
	console.log("NOCONNECT");
	return false;
}

function Warning(props){
	return(
		<Div className = "TextRegisterMenuSignIn">
			{props.title}
		</Div>
	)
}

function ButtonConfirm(props){
	return(
		<Div className = "ButtonDivRegisterMenu">
			<Button className = "ButtonRegisterMenu" id = "but1" size="xl" mode="outline" onClick={() => props.confirm()} data-to="mainMenu">
				<Div className = "TextButtonRegisterMenu">ВОЙТИ</Div>
			</Button>
		</Div>
	)
}

function TextRegistraton(props){
	return(	
		<Div className = "TextRegisterMenuCode">
			Регистрационный код
		</Div>
	)
}

class MainArea extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			title : ""
		  };
		  this.handleClickOnButtonConfirm = this.handleClickOnButtonConfirm.bind(this);
	  }
	  async handleClickOnButtonConfirm(){
		  var condition = await IsRegistration();
		  console.log(condition);
		  if (condition){
			  console.log("yes")
			  this.props.go();
			  return;
		  }
		  this.setState({title : "Введен неверный код"});
	  }
	  render(){
		  return(
			<Div className="MainRegisterMenu">
				<Warning title = {this.state.title} />
				<TextRegistraton />
				<Input id ="input-id" className = "InputRegisterMenu" type="text" placeholder= "Введите регистрационный код"/>
				<ButtonConfirm confirm = {this.handleClickOnButtonConfirm} />
			</Div>
		  )
	  }
}

const RegisterMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader>
			<Div className = "PanelHeaderRegisterMenu">
				Регистрация
			</Div>
		</PanelHeader>
		<MainArea go = {props.go}/>
	</Panel>
);
