import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import './StyleMainMenu.css';
import cloud from '../img/cloud.png';
import Work from '../img/Work.png';

const osName = platform();

const MainMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader>
			 <Div className = "PanelHeaderMainMenu">
				Меню
			</Div>
		</PanelHeader>
		<Div className="BaseMainMenu">
			<Div className = "TextMainMenuWelcome">
				Добро пожаловать!
			</Div>
			<Div className = "ButtonBlocksMainMenu">
				<Button className = "ButtonMainMenu" size="xl" mode="outline" onClick={props.goRecord} data-to="scheduleMenu">
					<Div className = "TextButtonMainMenu">ЗАПИСАТЬСЯ</Div>
				</Button>
				<Button className = "ButtonMainMenu" size="xl" mode="outline" onClick={props.goMyRecord} data-to="myRecordMenu"> 
					<Div className = "TextButtonMainMenu">МОИ ЗАПИСИ</Div>
				</Button>
				<Button className = "ButtonMainMenu" size="xl" mode="outline" onClick={props.go} data-to="myQRCodeMenu">
					<Div className = "TextButtonMainMenu">МОЙ QR-КОД</Div>
				</Button>
			</Div>
			<Div className = "ImgDivMainMenuWork">
				<img className = "ImgMainMenuWork" src={Work}></img>
			</Div>			
		</Div>
	</Panel>
);


MainMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default MainMenu;