import "core-js/features/map";
import "core-js/features/set";
import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import App from "./App";

var urlUsers = "https://cfe6e15b1fd0.ngrok.io/databaseUsersLearn";

async function IsAuthorization(userId){
	let response = await fetch(urlUsers, {
    	credentials: 'same-origin',  // параметр определяющий передвать ли разные сессионные данные вместе с запросом
    	method: 'POST',              // метод POST
    	body: JSON.stringify({idVk: userId}),  // типа запрашиаемого документа
    	headers: new Headers({
      		'Content-Type': 'application/json'
    	}),
	});
	if (response.ok) { 
  		let data = await response.json();
		if (data.length > 0){
			console.log(data);
			return true;
		}
		console.log("NO Data");
		return false;
	} 
	console.log("No connect");
	return false;
}


// Init VK  Mini App
bridge.send("VKWebAppInit");

async function Start(){
  var currentUser = await bridge.send('VKWebAppGetUserInfo');
  var condition = await IsAuthorization(currentUser.id);
  var activePage = condition ?'mainMenu' : 'registerMenu';
  ReactDOM.render(<App active = {activePage} />, document.getElementById("root"));
}

Start();

if (process.env.NODE_ENV === "development") {
  import("./eruda").then(({ default: eruda }) => {}); //runtime download
}
